import './order.scss';

import SelfServiceForm from './order-forms/SelfServiceForm';
import DeliveryForm from './order-forms/DeliveryForm';

import {useState, useEffect} from 'react';

const Order = ()=>{
    useEffect(()=>{
        //   ,      (,     )
    window.scrollTo(0, 0);
    }, [])

    const [isDelivery, setIsDelivery] = useState(true);
    return (
        <>
        <h2> </h2>

        <div className="order__way-to-get">
            <button 
            onClick={()=>{setIsDelivery(true)}}
            className={isDelivery ? 'way-to-get--active' : ''}></button>
            <button 
            onClick={()=>{setIsDelivery(false)}}
            className={isDelivery ? 'order__self-service' : 'order__self-service way-to-get--active'}></button>
        </div>
        {/*     */}
        {isDelivery ? <DeliveryForm/> : <SelfServiceForm/>}

        </>
    )
}


export default Order;